import * as React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getUserProfileHeader, getStatus, updateStatus, savePhoto, saveProfile} from "../../redax/profile-reducer";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {withAuthRedirect} from "../../HOC/withAuthRedirect";
import {compose} from "redux";
import {profileType, routerType} from "../../types/types";
import {AppStateType} from "../../redax/redux-store";


type MapPropsType = {
    profile: profileType
    status: string
    authorizedUserId: number
    isAuth: boolean
}
type DispatchType = {
    getUserProfileHeader: (userId: number) => void
    getStatus: (userId: number) => void
    updateStatus: (status: string) => void
    saveProfile: (profile: profileType) => Promise<any>
    savePhoto: (file: File) => void
}
export type WithRouterType = {
    router: routerType
    history: Array<string>
}
type OllPropsType = DispatchType & MapPropsType & WithRouterType

//RouteComponentProps<withRouterType>



class ProfileContainer extends React.Component<OllPropsType> {

    refreshProfile(){
        let userId = this.props.router.params.userId;
        if(!userId) {
            userId = this.props.authorizedUserId
            if(!userId){
                this.props.history.push("/login")
            }
        }
        this.props.getUserProfileHeader(userId)
        this.props.getStatus(userId)
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps: OllPropsType, prevState: OllPropsType) {
        if(this.props.router.params.userId != prevProps.router.params.userId){
            this.refreshProfile()
        }
    }

    render () {
      return (
      <Profile {...this.props} status={this.props.status} profile={ this.props.profile }
               //router={ this.props.router}
               isOwner={!this.props.router.params.userId} userId={ this.props.router.params.userId}
               updateStatus={this.props.updateStatus} savePhoto={this.props.savePhoto} saveProfile={this.props.saveProfile}/>
      )
  }
}

let mapStateToProps = (state: AppStateType) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.userId,  //userId
    isAuth: state.auth.isAuth
})


function withRouter(Component: any): React.FC<OllPropsType> {
    function ComponentWithRouterProp(props: any) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }
    return ComponentWithRouterProp;
}

export default compose<React.ComponentType> ( connect( mapStateToProps,
        { getUserProfileHeader, getStatus, updateStatus, savePhoto, saveProfile } ),
    withRouter, withAuthRedirect) (ProfileContainer)





