import * as React from 'react';
import ProfileInfo, {PropsType} from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";


const Profile: React.FC<PropsType> = (props) => {
// debugger

  return (
    <div>
      <ProfileInfo status={props.status} updateStatus={props.updateStatus} profile={ props.profile } saveProfile={props.saveProfile}
                   //router={props.router}
                   userId={props.userId} isOwner={props.isOwner} savePhoto={props.savePhoto} />
      <MyPostsContainer />
    </div>
  )
}

export default Profile;