import * as React from 'react';
import s from './MyPosts.module.css'
import Post from "./Post/Post";
import {postType} from "../../../types/types";
import {FormMyPost} from "./FormMyPost/FormMyPost";


type MyPostsPropsType = {
    posts: Array<postType>
    addPostDispatch: (areaMyPost: string) => void
}

const MyPosts: React.FC<MyPostsPropsType> = (props) => {
    // shouldComponentUpdate, PureComponent, memo

    let postsElements =
        [...props.posts]
            .reverse()
            .map( p => <Post key={p.id} massage={p.message} likeCount={p.likeCount} /> )

    const addNewMes = (value: string) => {
        console.log(value)
        props.addPostDispatch(value)
    }
    return (
        <div className={s.myPost}>
            <h2>My post</h2>
            <div className={s.area}>
                <FormMyPost addNewMes={addNewMes} />
            </div>
            <div>
                New post
                { postsElements }
            </div>
        </div>

  )
}


export default MyPosts;




