import * as React from 'react';
import {connect} from "react-redux";
import {postType} from "../../../types/types";
import {actions} from "../../../redax/profile-reducer";
import MyPosts from "./MyPosts";
import {AppStateType} from "../../../redax/redux-store";


type mapStateProps = {
    posts: Array<postType>
}
type mapDispatchProps = {
    addPostDispatch: (newPostText: string) => void
}

let mapStateToProps = (state: AppStateType): mapStateProps => {
    return {
        posts: state.profilePage.posts
    }
}


const MyPostsContainer = connect<mapStateProps, mapDispatchProps, {}, AppStateType>(mapStateToProps,
    { addPostDispatch: actions.addPostKey }) (MyPosts);

export default MyPostsContainer;