import React from "react";
import {Field, Form, Formik} from "formik";
import s from "./FormMyPost.module.css";
import * as Yup from "yup";

type ValuesType = {
    areaMyPost: string
}
type FormPropsType = {
    addNewMes: (values: string) => void
}
const DisplayingErrorMes = Yup.object().shape({
    areaMyPost: Yup.string()
        .min(0, 'Too Short!')
        .max(100, 'Too Long!')
        .required('Too Short!'),
});


export const FormMyPost: React.FC<FormPropsType> = (props) => {

    let onSubmit = (values: ValuesType, {setSubmitting}: { setSubmitting: (isSubmitting: boolean) => void }) => {
        props.addNewMes(values.areaMyPost)
        setSubmitting(false)
        values.areaMyPost = ''
    }

    return <div>
        <Formik
            initialValues={{areaMyPost: ''}}
            validationSchema={DisplayingErrorMes}
            onSubmit={onSubmit}
        >
            {({isSubmitting, errors}) => (
                <Form>
                    <div>
                        <Field as="textarea" name="areaMyPost" placeholder='Enter here'
                               className={`${(errors.areaMyPost) ? s.formErrorArea : ''}`}/>
                        <p></p>
                        {errors.areaMyPost && <div className={s.formMyPostError}>{errors.areaMyPost}</div>}
                    </div>
                    <p></p>
                    <button className={s.sendBut} type="submit" disabled={isSubmitting}>
                        Send
                    </button>
                </Form>
            )}
        </Formik>
    </div>
}