import React from "react";
import {ChangeEvent, useEffect, useState} from "react";


type OwnPropsType = {
    status: string
    updateStatus: (status: string) => void
}

const ProfileStatusWithHooks: React.FC<OwnPropsType> = (props) => {

    let [editeMode, setEditeMode] = useState(false)
    let [status, setStatus] = useState(props.status)

    useEffect(() => {
        setStatus(props.status)
    }, [props.status])

    const doubleClick = () => {
        setEditeMode(  true)
    }

    const blur = () => {
        setEditeMode( false)
        props.updateStatus(status)
    }

    const onStatusChang = (e: ChangeEvent<HTMLInputElement>) => {
        setStatus( e.currentTarget.value)
    }

        return (
            <div>
                { !editeMode &&
                    <div>
                        <b>My dumi:</b> <span onDoubleClick={ doubleClick }> {props.status || '-no status-'} </span>
                    </div>
                }
                { editeMode &&
                    <div>
                        <input onChange={onStatusChang} autoFocus={true} value={status || '--no status--'} onBlur={ blur } />
                    </div>
                }
            </div>
        )
}

export default ProfileStatusWithHooks