import * as React from "react";
import {Navigate} from "react-router-dom";
import {connect} from "react-redux";
import {compose} from "redux";
import {AppStateType} from "../redax/redux-store";


let mapStateToPropsForRedirect = (state: AppStateType) => ({
    isAuth: state.auth.isAuth
})

type mapStateType = {
    isAuth: boolean
}

export function withAuthRedirect<WSP> (WrapperComponent: React.ComponentType<WSP>) {
    function RedirectComponent(props: WSP & mapStateType) {

        if (!props.isAuth) return <Navigate to={"/login"}/>

        return <WrapperComponent { ...props} />
    }

    let ConnectedAuthRedirectComponent = compose<React.ComponentType> (connect(mapStateToPropsForRedirect)) (RedirectComponent)

    return ConnectedAuthRedirectComponent
}

