import React from 'react';
import s from './Post.module.css'

type PropsType = {
    massage: string
    likeCount: number
}

const Post: React.FC<PropsType> = (props) => {

  return (
      <div className={s.item}>
        <img src='data:image/jpeg;base64,' />
        { props.massage }
        <div className={s.like}>
          <span>like { props.likeCount }</span>
        </div>
      </div>
  )
}

export default Post;